.header {
    font-family: "Flexo-Regular", arial, sans-serif;
    width: 100%;
    text-align: center;
    background-color: #1A202C;
    display: flex;
    justify-content: center;
    align-items: center;
}

.header h1 {
    color: white;
    padding: 20px;
    font-size: 60px;
}

.img-header {
    width: 70px;
    height: 70px;
}

@media screen and (max-width: 600px) {
    .header h1 {
        padding: 20px 10px;
        font-size: 30px;
    }

    .img-header {
        width: 40px;
        height: 40px;
    }
}