.background-display {
    background-image: url(./images/pokemon-unite-key-art-fight.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 5% 0;
}

.display {
    width: 100%;
    margin: 0 auto;
    max-width: 1100px;
    padding: 30px 2%;
    border-radius: 10px;
    background-color: rgba(26, 32, 44, 0.5);
}

.display h2 {
    padding: 2%;
    font-size: 45px;
    text-align: center;
    color: #ef5350;
}

.display h2 img {
    width: 35px;
}

.display input[type=text] {
    width: 85%;
    padding: 10px;
    border: 1px solid #1A202C;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}

.display input[type=submit] {
    width: 10%;
    padding: 10px;
    cursor: pointer;
    border: 1px solid #1A202C;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}

.display-boxes {
    width: 100%;
    margin: 20px 0;
    display: flex;
    flex-wrap: wrap;
}

.pokemon-box {
    font-family: 'flexo-medium';
    border-radius: 10px;
    width: 29%;
    height: 350px;
    margin: 2% 2%;
    background-color: rgb(230, 230, 230);
    cursor: pointer;
    transition: transform 0.3s ease;
    -webkit-box-shadow: 0px 0px 40px -9px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 40px -9px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 0px 40px -9px rgba(0, 0, 0, 0.75);
}

.pokemon-box.virada {
    transform: rotateY(180deg) !important;
}

.pokemon-box img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.p-pokemon-box {
    width: 100%;
}

.p-pokemon-box p {
    width: 15%;
    margin-bottom: 4%;
    margin-right: 4%;
    padding-bottom: 2px;
    text-align: center;
    background-color: #ef5350;
    border-radius: 10px;
    margin-left: auto;
}

.pokemon-box div {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transform: rotateY(180deg);
}

.informationsDisplay p {
    padding: 10px;
    direction: rtl;
}

.name-informations {
    width: 60%;
    background-color: rgb(255, 90, 90);
    color: white;
    border-radius: 10px;
}

.uppercase {
    text-transform: uppercase;
}

@media screen and (max-width:1024px) {
    .display {
        width: 90%;
    }
}

@media screen and (max-width: 820px) {
    .display {
        width: 90%;
    }

    .pokemon-box {
        border-radius: 10px;
        width: 29%;
        height: 250px;
        margin: 2% 2%;
        background-color: rgb(230, 230, 230);
        cursor: pointer;
        transition: transform 0.3s ease;
    }

    .informationsDisplay p {
        padding: 2px;
        font-size: 15px;
    }

    .informationsDisplay ul li {
        font-size: 15px;
    }

    .name-informations {
        width: 60%;
        background-color: rgb(255, 90, 90);
        color: white;
        border-radius: 10px;
    }
}

@media screen and (max-width: 600px) {
    .display {
        width: 90%;
    }

    .pokemon-box {
        border-radius: 10px;
        width: 29%;
        height: 200px;
        margin: 2% 2%;
        background-color: rgb(230, 230, 230);
        cursor: pointer;
        transition: transform 0.3s ease;
    }

    .display input[type=text] {
        width: 80%;
        padding: 10px;
    }

    .display input[type=submit] {
        width: 15%;
        padding: 10px;
        cursor: pointer;
    }

    .informationsDisplay p {
        padding: 2px;
        font-size: 10px;
    }

    .informationsDisplay ul li {
        font-size: 10px;
    }

    .name-informations {
        width: 60%;
        background-color: rgb(255, 90, 90);
        color: white;
        border-radius: 10px;
    }

    .p-pokemon-box p {
        width: 20%;
        margin-bottom: 4%;
        margin-right: 4%;
        padding-bottom: 2px;
        text-align: center;
        background-color: #ef5350;
        border-radius: 10px;
        margin-left: auto;
    }
}

@media screen and (max-width: 550px) {

    .pokemon-box {
        border-radius: 10px;
        width: 100%;
        height: 300px;
        margin: 4% 15%;
        background-color: rgb(230, 230, 230);
        cursor: pointer;
        transition: transform 0.3s ease;
    }

    .display input[type=text] {
        width: 70%;
        padding: 10px;
    }

    .display input[type=submit] {
        width: 25%;
        padding: 10px;
        cursor: pointer;
    }

    .informationsDisplay p {
        padding: 2px;
        font-size: 15px;
    }

    .informationsDisplay ul li {
        font-size: 15px;
    }

    .name-informations {
        width: 60%;
        background-color: rgb(255, 90, 90);
        color: white;
        border-radius: 10px;
    }

    .pokemon-box img {
        width: 80%;
        height: 100%;
        object-fit: cover;
    }

    .p-pokemon-box p {
        width: 20%;
        margin-right: 4%;
        padding-bottom: 2px;
        text-align: center;
        background-color: #ef5350;
        border-radius: 10px;
        margin-left: auto;
    }
}

@media screen and (max-width:450px) {
    .pokemon-box img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .p-pokemon-box p {
        width: 20%;
        margin-right: 4%;
        text-align: center;
        background-color: #ef5350;
        border-radius: 10px;
        margin-left: auto;
    }
}

@media screen and (max-width:360px) {
    .display form h2 {
        padding-bottom: 20px;
    }

    .pokemon-box {
        height: 250px;
    }
}

@media screen and (max-width:300px) {
    .pokemon-box {
        height: 220px;
    }
}

